@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(248, 246, 253);
}

body,
input,
textarea,
button {
  font-family: "Oswald", sans-serif;
}

a {
  font: white;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 350px;
  margin: 0 auto;
  height: 70vh;
  
}
/* overskrift og tekst  */

.overskrift{
  position: relative;
  margin-left: 36%;
}

.underskrift {
  position: relative;
  margin-left: 25%;
}

/* Contact.js */


.form {
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}



.form > button {
  
  padding: 20px;
  border: none;
  background-color: brown;
  font-weight: 500;
  font-size: 20px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 10px;
}

.form > button:hover {
  background-color: rgb(0, 0, 196);
}

/* contact.js slut  */

/* Køb knap  */

.order {
  height: 1vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}


.orderknap {
  
  padding: 20px;
  border: none;
  background-color: brown;
  font-weight: 500;
  font-size: 20px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 10px;
}

.order > .orderknap:hover {
  background-color: rgb(0, 0, 196);
}
